import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Store, select } from '@ngrx/store';
import { MenuService } from 'src/app/services/menu.service';
import { PegawaiDetailService } from '../../profile/service/pegawai-detail.service';
import { RouterOutlet } from '@angular/router';
import * as Reducers from 'src/app/store/reducers'

@Component({
  selector: 'app-complaints',
  templateUrl: './complaints.component.html',
  styleUrls: ['./complaints.component.scss']
})
export class ComplaintsComponent implements OnInit {

    settings$: Observable<any>
    menuLayoutType: string
    isContentNoMaxWidth: boolean
    isAppMaxWidth: boolean
    isGrayBackground: boolean
    isSquaredBorders: boolean
    isCardShadow: boolean
    isBorderless: boolean
    isTopbarFixed: boolean
    isGrayTopbar: boolean
    routerAnimation: string

    constructor(
        private store: Store<any>,
        private menuService: MenuService,
        private pegawaiDetailService: PegawaiDetailService
    ) {
        this.store.pipe(select(Reducers.getSettings)).subscribe(state => {
            this.menuLayoutType = state.menuLayoutType
            this.isContentNoMaxWidth = state.isContentNoMaxWidth
            this.isAppMaxWidth = state.isAppMaxWidth
            this.isGrayBackground = state.isGrayBackground
            this.isSquaredBorders = state.isSquaredBorders
            this.isCardShadow = state.isCardShadow
            this.isBorderless = state.isBorderless
            this.isTopbarFixed = state.isTopbarFixed
            this.isGrayTopbar = state.isGrayTopbar
            this.routerAnimation = state.routerAnimation
        })
    }
    id_pegawai = '28fd0999-a842-4a79-9103-497f2545eb26';

    menuData: any = []
    ngOnInit() {
        this.pegawaiDetailService.setIdpegawai(this.id_pegawai);

        this.menuData = [

            {
                title: 'All',
                key: 'All',
                icon: 'fe fe-bookmark',
                url: 'list/all/all',
            },

            {
                title: 'HR',
                key: 'HR',
                icon: 'fe fe-user',
                children: [
                    {
                        title: 'Draft',
                        key: 'draft',
                        icon: 'fe fe-bookmark',
                        url: 'list/hr/draft',
                    },
                    {
                        title: 'Open',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/hr/confirm',
                    },
                    {
                        title: 'Process',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/hr/approved',
                    },
                    {
                        title: 'Close',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/hr/close',
                    },
                ]
            },

            {
                title: 'GA',
                key: 'GA',
                icon: 'fe fe-user',
                children: [
                    {
                        title: 'Draft',
                        key: 'draft',
                        icon: 'fe fe-bookmark',
                        url: 'list/GA/draft',
                    },
                    {
                        title: 'Open',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/GA/confirm',
                    },
                    {
                        title: 'Process',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/GA/approved',
                    },
                    {
                        title: 'Close',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/GA/close',
                    },
                ]
            },
            {
                title: 'IT',
                key: 'IT',
                icon: 'fe fe-user',
                children: [
                    {
                        title: 'Draft',
                        key: 'draft',
                        icon: 'fe fe-bookmark',
                        url: 'list/IT/draft',
                    },
                    {
                        title: 'Open',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/IT/confirm',
                    },
                    {
                        title: 'Process',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/IT/approved',
                    },
                    {
                        title: 'Close',
                        key: 'Confirm',
                        icon: 'fe fe-bookmark',
                        url: 'list/IT/close',
                    },
                ]
            },

         ]
        this.menuService.menu.next(this.menuData)
        this.menuService.subBar_info.next({ parent: 'HR', child: 'Kalender' })
    }

    routeAnimation(outlet: RouterOutlet, animation: string) {
        if (animation === this.routerAnimation) {
            return outlet.isActivated && outlet.activatedRoute.routeConfig.path
        }
    }

}
